<template>
    <el-table :data="categoryTree" row-key="id" :tree-props="{ children: 'children', hasChildren: 'hasChildren' }">
        <el-table-column label="接口名称" prop="name" align="left"></el-table-column>
        <el-table-column align="right" label="操作">
            <template #default="scope">
                <el-button type="primary" plain icon="el-icon-search" size="mini" v-if="scope.row.level >= 2" @click="$emit('getDetails', scope.row, 'json')">JSON</el-button>
                <el-button type="primary" plain icon="el-icon-search" size="mini" @click="$emit('getDetails', scope.row, 'markdown')">Markdown</el-button>
                <el-button type="primary" plain icon="el-icon-plus" size="mini" v-if="scope.row.level <= 2" @click="$emit('addCategory', scope.row)">新增</el-button>
                <el-button type="primary" plain icon="el-icon-edit" size="mini" @click="$emit('updateCategory', scope.row)">修改</el-button>
                <el-button type="danger" plain icon="el-icon-delete" size="mini" @click="$emit('removeCategory', scope.row)">删除</el-button>
            </template>
        </el-table-column>
    </el-table>
</template>

<script>
export default {
    name: 'CategoryLists',
    props: ['categoryTree'],
}
</script>

